.nothing{
   width: 100%;
   height: 264px;
   background: url(../../img/nothingandlook/bg.jpg);
}
.nothing__content{
   margin:0 auto;
   width: 570px;
   padding-top: 48px;
}
.nothing__content__title{
   color: rgb(255, 255, 255);
   font-size: 24px;
   font-weight: 700;
   font-family: 'Barlow', sans-serif;
   line-height: 30px;
   text-align: center;
}
.nothing__content__text{
   font-size: 18px;
   font-family: 'Barlow', sans-serif;
   line-height: 24px;
   color: rgb(255, 255, 255);
   text-align: center;
   margin-top: 6px;
}
.nothing__content__form{
   margin-top: 24px;
}
.nothing__content__form__inp{
   background-color: rgba(28,28,28,0);
   border: 2px solid rgba(255, 255, 255, 0.7);
   border-radius: 3px;
   font-size: 18px;
   font-family: 'Barlow', sans-serif;
   padding: 10px 20px;
   line-height: 24px;
   color: white;
   width: 232px;
}
.nothing__content__form__inp:focus{
   border: 2px solid rgba(255, 255, 255, 0.7);
}
.nothing__content__form__inp::-ms-clear {
   width: 0;
   height: 0;
}
input::-webkit-input-placeholder { 
   color: rgba(255, 255, 255, 0.7) 
}
input {
   outline:none;
}
.nothing__content__form__btn{
   background-color: rgba(255, 255, 255, 0.85);
   border-radius: 3px;
   border: none;
   font-size: 18px;
   line-height: 24px;
   color: rgb(34, 34, 34);
   padding: 12px 30px;
   font-family: 'Barlow', sans-serif;
   box-sizing: border-box;
   margin-left: 15px;
   cursor: pointer;
   transition: background-color .1s;
}
.nothing__content__form__btn:hover{
   background-color: white;
}
@media screen and (min-width: 420px) and (max-width: 989px){
   .nothing{
      height: 378px;
   }
   .nothing__content{
      width: 390px;
   }
   .nothing__content__form{
      margin-top: 24px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
   }
   .nothing__content__form__btn{
      margin-top: 12px;
      margin-left: 0;
   }
}